.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.mycard {
  margin: 10px;
  height: 400px;
}
.mycard img{
  height: 180px;
  width: 100%;
}
#headingNews {
  margin: 25px 10px;
  font-size: 50px;
  font-family: system-ui;
  font-weight: 400;
}
.myButtons{
  display: flex;
  justify-content: space-between;
  margin: 30px 10px;
}
.MoveBar{
  border: solid 1px black;
  width: 20%;
  padding: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
  animation-name: scroll;
  animation-duration: 10s;
}
@keyframes scroll{
  from{
    width:20%
  }
  to{
    width:80%;
  }
}
.mysearch{
  display: flex;
  margin: 25px 27px;
}